import React from 'react'
import Card from 'react-bootstrap/Card'
//import Card from './CardUI';
import CardColumns from 'react-bootstrap/CardColumns'
import Data from '../Data/Data'
//import Col from 'react-bootstrap/Col'
//import './card-sty
import './card-style.css'
import Button from 'react-bootstrap/Button'
import cat from '../assets/custom.jp2'
import LazyLoad from 'react-lazy-load'
function Cardses(props) {
    const render = (card,index ) => {
            return (
                <Card style = {{ backgroundColor : 'white' }}>
                    <LazyLoad
                        debounce={false}
                    >
                        <Card.Img  src = {card.image}/>
                    </LazyLoad>
                    <Card.Body>
                        <Card.Title className = " font2" ><center><h2>{card.title}</h2></center></Card.Title>
                    <Card.Text >
                        {card.text}
                    </Card.Text>
                    {
                        card.tag === true &&
                            <center><Button href={card.link} target="_blank"  variant="outline-primary" size="lg">{card.check}!</Button></center>
                    }
                    
                    </Card.Body>
  <Card.Footer className="text-muted">
      <center>
        {card.tags.map((variant,idx)=><small  key={idx} className="tab text">{variant}&nbsp;&nbsp;</small>)}
      </center>
  </Card.Footer>
                    
                </Card>

            );
    };

    return Data[props.data].map(render)

};
function Cardse(props) {
    let  eng = "Checa mi github"
    let  esp = "Check my github"
    return(
        <CardColumns>
            <Cardses data ={props.data}/>
            <Card >
        <Card.Body >
            <LazyLoad>
            <Card.Img src={cat} alt="Card image"/>
            </LazyLoad>
        </Card.Body>
                <h4>
                    <a  rel="noopener noreferrer" target="_blank" href = "https://github.com/yollotltamayo">
                        <center><small className="text-muted">{props.data !== 1?esp:eng}</small></center>
                    </a>
                </h4>
        </Card>
        </CardColumns>
    )
}
            
export default Cardse;
